(function() {
  angular.module('doorapi').directive('acmeNavbar', function() {
    var NavbarController, directive;
    NavbarController = function(moment) {
      'ngInject';
      var vm;
      vm = this;
      vm.relativeDate = moment(vm.creationDate).fromNow();
    };
    return directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
        creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };
  });

}).call(this);



(function() {
  angular.module('doorapi').directive('acmeMalarkey', function() {
    var MalarkeyController, directive, linkFunc;
    MalarkeyController = function($log, githubContributor) {
      'ngInject';
      var activate, getContributors, vm;
      vm = this;
      activate = function() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      };
      getContributors = function() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;
          return vm.contributors;
        });
      };
      vm.contributors = [];
      activate();
    };
    linkFunc = function(scope, el, attr, vm) {
      var typist, watcher;
      watcher = void 0;
      typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });
      el.addClass('acme-malarkey');
      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause()["delete"]();
      });
      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause()["delete"]();
        });
      });
      scope.$on('$destroy', function() {
        watcher();
      });
    };
    return directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };
  });

}).call(this);



(function() {
  angular.module('doorapi').factory('githubContributor', function($log, $http) {
    'ngInject';
    var apiHost, getContributors, service;
    apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';
    getContributors = function(limit) {
      var getContributorsComplete, getContributorsFailed;
      if (limit == null) {
        limit = 30;
      }
      getContributorsComplete = function(response) {
        return response.data;
      };
      getContributorsFailed = function(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      };
      return $http.get(apiHost + '/contributors?per_page=' + limit).then(getContributorsComplete)["catch"](getContributorsFailed);
    };
    return service = {
      apiHost: apiHost,
      getContributors: getContributors
    };
  });

}).call(this);



(function() {
  angular.module('doorapi').controller('UserSessionsCtrl', function($scope, $location, $log) {
    if ($location.search().err === "unauthorized") {
      $scope.error = "You don't have permission to access this area! Please login first.";
      $location.search("err", null);
    }
    return $scope.$on('auth:login-error', function(ev, reason) {
      return $scope.error = reason.errors[0];
    });
  });

}).call(this);



(function() {
  angular.module('doorapi').controller('MainController', function($timeout, webDevTec, toastr) {
    'ngInject';
    var activate, getWebDevTec, showToastr, vm;
    vm = this;
    activate = function() {
      getWebDevTec();
      $timeout((function() {
        vm.classAnimation = 'rubberBand';
      }), 4000);
    };
    showToastr = function() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    };
    getWebDevTec = function() {
      vm.awesomeThings = webDevTec.getTec();
      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    };
    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1452776807354;
    vm.showToastr = showToastr;
    activate();
  });

}).call(this);



(function() {
  angular.module('doorapi').controller("GroupsCtrl", function($scope, Group) {
    return $scope.groups = Group.query();
  });

}).call(this);



(function() {
  angular.module('doorapi').factory('Group', function($resource) {
    return $resource('/api/groups/:id', {
      id: "@id"
    }, {
      update: {
        method: 'PUT'
      }
    });
  });

}).call(this);



(function() {
  angular.module('doorapi').run(function($log) {
    'ngInject';
    return $log.debug('runBlock end');
  }).run(function($rootScope, $location, $log) {
    $rootScope.$on('auth:login-success', function() {
      return $location.path('/');
    });
    return $rootScope.$on('$stateChangeError', function(event, toState, toParams, fromState, fromParams, error) {
      event.preventDefault();
      $log.log(error);
      if (error.reason === "unauthorized") {
        return $location.path('/sign_in').search({
          err: "unauthorized"
        });
      }
    });
  });

}).call(this);



(function() {
  angular.module('doorapi').config(function($stateProvider, $urlRouterProvider, $locationProvider) {
    'ngInject';
    $stateProvider.state('home', {
      url: '/',
      templateUrl: 'app/main/main.html',
      controller: 'MainController',
      controllerAs: 'main'
    }).state("groups", {
      url: "/groups",
      templateUrl: "app/views/groups.html",
      controller: "GroupsCtrl",
      resolve: {
        auth: function($auth) {
          return $auth.validateUser();
        }
      }
    }).state("sign_in", {
      url: "/sign_in",
      templateUrl: "app/views/user_sessions/new.html",
      controller: "UserSessionsCtrl"
    });
    $urlRouterProvider.otherwise('/');
    return $locationProvider.html5Mode({
      enabled: true
    });
  });

}).call(this);



